* {
  font-family: Arial, sans-serif;
}

body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
}

details {
  margin: 1em;
}

details > summary > h2 {
  cursor: pointer;
  display: inline;
}

details[open] {
  margin-bottom: 4em;
}

div#controls ul {
  margin: 0;
  padding-top: 0;
}

div#controls cite {
  background: #0003;
  border-radius: .5em;
  padding: .2em;
  font-family: Courier New, monospace;
  font-style: normal;
  line-height: 1.7em;
}

div#controls div#range {
  height: 10px;
  margin: 3em 0 8em 3em;
}

div#controls div#range .noUi-handle {
  width: 18px;
  height: 18px;
  top: -4.5px;
  right: -9px;
}

div#controls div#range .noUi-handle:before, div#controls div#range .noUi-handle:after {
  display: none;
}

div#controls div#range .noUi-marker {
  top: 3px;
}

div#controls div#range .noUi-value {
  transform-origin: center;
  top: -1em;
  transform: rotate(-45deg)translateX(-110%);
}

body > div {
  padding: 5vh;
  font-size: 80%;
}

h1 {
  margin-top: 2em;
}

div #uploadCsv {
  margin: 1em 0;
}

table {
  border-collapse: separate;
  border-spacing: 0;
  background-color: #fff;
  border-radius: 10px;
  width: 100%;
  overflow: hidden;
  box-shadow: 0 4px 8px #0000001a;
}

th, td {
  border: 1px solid #ddd;
  padding: 12px;
}

th {
  color: #fff;
  background-color: #346336;
  font-weight: bold;
}

table#csvMeta {
  width: auto;
}

table#csvMeta th {
  text-align: right;
}

table#csvMeta td {
  text-align: left;
}

table#csv div.canvasHolder {
  aspect-ratio: 1;
  width: 100%;
  max-width: 10vw;
  height: auto;
  max-height: 10vw;
  margin: auto;
}

td.split {
  background-color: #5f1d1d;
  border: none;
  padding: 0;
  padding: 5px !important;
}

tr.daySeparator td {
  text-align: left;
  background-color: #f0f0f0;
}

td {
  text-align: right;
}

table#intervals th, table#intervals td {
  padding-top: 5px;
  padding-bottom: 5px;
}

table#intervals th.error {
  background-color: red;
}

table#intervals th.insufficient {
  background-color: #8cb344;
}

ul#warnings {
  color: #fff;
  background-color: red;
  height: 20vh;
  margin: 0;
  font-size: 80%;
  display: none;
  overflow-y: scroll;
}

th.distribution, th.consumer {
  word-break: break-word;
  position: relative;
}

th.distribution input[type="checkbox"], th.consumer input[type="checkbox"] {
  width: auto;
  margin: 0;
  padding: 0;
  display: inline;
}

th.distribution div.isolate, th.consumer div.isolate {
  user-select: none;
  cursor: pointer;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  right: 0;
}

tr.csvHeaderRow input {
  width: 100%;
  min-width: 0;
}

table#computation input {
  text-align: right;
}

table#computation input[type="number"] {
  text-align: right;
  width: 70%;
}

table#computation tr.optimize td, table#computation tr.compute td {
  text-align: center;
}

table#computation tr.optimize td input#submitAllocation, table#computation tr.compute td input#submitAllocation {
  width: 50%;
}
